	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Living in Europe: Czechia or Czech Republic?","date":"12 May 2004","slug":"/2004-05-12-living-in-europe-czechia-or-czech-republic/","tags":"Y2004"},"html":"<p><a title=\"Maybe they need a symbol, like Prince used to have\" href=\"http://www.livingineurope.net/archives/000253.html\">Living in Europe: Czechia or Czech Republic?</a></p>"},"next":{"fields":{"title":"I'm now a Medienkritiker","date":"12 May 2004","slug":"/2004-05-12-im-now-a-medienkritiker/"}},"previous":{"fields":{"title":"DSL is female","date":"13 May 2004","slug":"/2004-05-13-dsl-is-female/"}}},{"node":{"fields":{"title":"I'm now a Medienkritiker","date":"12 May 2004","slug":"/2004-05-12-im-now-a-medienkritiker/","tags":"Y2004"},"html":"<p>Well, not really. But <a title=\"Davids Medienkritik\" href=\"http://medienkritik.typepad.com/blog/\">Davids Medienkritik</a> did just post a <a title=\"Davids Medienkritik: We Need Your Help!\" href=\"http://medienkritik.typepad.com/blog/2004/05/we_need_your_he.html\">plea to help translate</a> posts into English, and I'll be helping out now and again (once a week or so). </p>\n<p>Why, espcially considering that David's views are, um, somewhat to the right of mine? Because it's a good blog, and it starts good conversations. Even if Germans and Americans tend to disagree, at Medienkritik they're at least talking to one another. As a bilingual expat, one thing I can offer to the community is to translate things that are interesting, and David's points are always interesting and worth considering. </p>\n<p>Translation is hard work and I am by no means a profressional translator, but at least the <a title=\"Davids Medienkritik: Bush's Down ... Oops, He's Up! / Bush sinkt ... Hoppla, er steigt!\" href=\"http://medienkritik.typepad.com/blog/2004/05/bushs_down_oops.html\">first post</a> was fun. I felt like I was behind enemy lines, writing headlines for the <em>Bild-Zeiting</em>.</p>"},"next":{"fields":{"title":"Phil Ringnalda","date":"11 May 2004","slug":"/2004-05-11-phil-ringnalda/"}},"previous":{"fields":{"title":"Living in Europe: Czechia or Czech Republic?","date":"12 May 2004","slug":"/2004-05-12-living-in-europe-czechia-or-czech-republic/"}}},{"node":{"fields":{"title":"Phil Ringnalda","date":"11 May 2004","slug":"/2004-05-11-phil-ringnalda/","tags":"Y2004"},"html":"<p><a title=\"Killer app! Killer app!\" href=\"http://philringnalda.com/blog/2004/05/bloglines_worms_its_way_into_mozilla.php\">Bloglines extension for Mozilla</a></p>"},"next":{"fields":{"title":"","date":"11 May 2004","slug":"/2004-05-11-muessen-nur-wollen/"}},"previous":{"fields":{"title":"I'm now a Medienkritiker","date":"12 May 2004","slug":"/2004-05-12-im-now-a-medienkritiker/"}}}],"pathPrefix":"","first":false,"last":false,"index":488,"pageCount":1101}}

